<template>
  <div style="padding:20px;">
    <div style="margin-bottom: 15px;overflow: hidden">
      <div style="float: left;">
        <Button type="primary" icon="ios-plus-empty" @click="handleAddArticleType">
          添加分类
        </Button>
      </div>
      <div style="float: right;">
      </div>
    </div>
    <Table :loading="loading" border ref="selection" :columns="articletypecolumns" :data="articletypedata"></Table>
    <div style="margin-top: 15px;overflow: hidden">
      <div style="float: left;">
      </div>
      <div style="float: right;">
        <!-- <Page :total="totalrecord" :page-size="pagesize" :page-size-opts="[20,40,60,80]" show-elevator show-sizer placement="top" @on-change="changePageNow" @on-page-size-change="changePageSize"></Page> -->
        <Page :total="totalrecord" :page-size="page.pagesize" :page-size-opts="[10,20,30,40]" show-elevator show-sizer placement="top" show-total @on-change='indexChange' @on-page-size-change='sizeChange'></Page>
      </div>
    </div>
    <Modal title="确认提示" v-model="showConfirm" width="300" class-name="vertical-center-modal" @on-ok="delRow">
      <p style="font-size:14px">确认要删除该条记录吗？</p>
    </Modal>
  </div>
</template>
<script>
export default {
  data() {
    return {
      currrowid: 0,
      showConfirm: false,
      totalrecord: 0,
      loading: true,
      page: {
        pagesize: 10,
        pageindex: 0,
      },
      articletypecolumns: [{
          title: '#',
          width: 60,
          align: 'center',
          render:(h,params)=>params.index+this.page.pagesize*this.page.pageindex+1
        },
        {
          title: '分类名称',
          key: 'name',
          align: 'center'
        },
        {
          title: '排序数值',
          key: 'sortvalue',
          align: 'center'
        },
        {
          title: '修改日期',
          key: 'updatetime',
          align: 'center'
        },
        {
          title: '操作',
          key: 'action',
          width: 150,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.showPage(params.row.id);
                  }
                }
              }, '查看'),
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small'
                },
                on: {
                  click: () => {
                    this.showConfirm = true;
                    this.currrowid = params.row.id;
                  }
                }
              }, '删除')
            ]);
          }
        }
      ],
      articletypedata: []
    }
  },
  mounted: function() {
    this.$sitemap.prvname = "内容分类";
    this.$sitemap.currname = '分类列表';
    this.$menuactive.open = ['ArticleBlock'];
    this.$menuactive.active = 'ArticletypeList';
    this.$Loading.start();
    this.getArticleTypeList();
  },
  watch: {
    page: {
      handler(newval) {
        this.getArticleTypeList()
      },
      deep: true,
    }
  },
  methods: {
    indexChange(index) {
      this.page.pageindex = index-1
    },
    sizeChange(size) {
      this.page.pagesize = size
    },
    getArticleTypeList: function() {
      let that = this;
      that.loading = true;
      this.$http.post("articletype/list", this.page).then(function(res) {
        that.articletypedata = res.data.data;
        that.loading = false;
        that.$Loading.finish();
        that.totalrecord = +res.data.totalcount;
      }).catch(function(res) {
        that.loading = false;
        that.$Loading.error();
      })
    },
    handleAddArticleType() {
      this.$router.push({ path: '/articletype/add' });
    },
    showPage(typeid) {
      this.$router.push({ path: '/articletype/edit', query: { id: typeid } });
    },
    delRow() {
      var that = this;
      var params = {
        typeid: this.currrowid
      };
      this.$http.post("articletype/del", params).then(function(res) {
        if (res.data.data == 1) {
          that.$Message.success('删除成功!');
          that.getArticleTypeList();
        } else {
          that.$Message.error('删除失败，请检查！');
        }
      }).catch(function(err) {
        that.$Message.error(error);
      });
    }
  }
}

</script>
<style lang="less">
.vertical-center-modal {
  display: flex;
  align-items: center;
  justify-content: center;

  .ivu-modal {
    top: 0;
  }
}

</style>



